package com.easternkite.eungabi.utils

import kotlin.uuid.ExperimentalUuidApi
import kotlin.uuid.Uuid

/**
 * get unique identifier.
 */
@OptIn(ExperimentalUuidApi::class)
val randomUuid: String get() = Uuid
    .random()
    .toString()
